/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  font-family: Arial, Helvetica, sans-serif;
  user-select: none;
  color: #000;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

h1, h2, h3 {
  margin: 0;
  font-weight: 300;
  text-align: center;
  color: #000;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

