.principal {
  width: 100%;
  height: 100vh;
}

.layer2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("/build/img/background.webp");
  background-attachment: fixed;
  background-position: center;
  transition: 2s;
}

.layer1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 110rem;
  height: 100rem;
  background: url("/build/img/background.webp");
  background-position: center;
  z-index: 1;
  transition: 2s;
  cursor: none;
}
.layer1:hover {
  box-shadow: 0 25px 60px black;
}
.layer1:hover ~ .layer2 {
  filter: blur(10px);
}

.dot {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  cursor: none;
  z-index: 1;
}